body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-poppins: Poppins;
  --font-inter: Inter;

  /* font sizes */
  --font-size-xs: 12px;
  --font-size-mini: 15px;

  /* Colors */
  --color-white: #fff;
  --color-silver: rgba(196, 196, 196, 0);
  --color-gold: #ffdd55;

  /* Gaps */
  --gap-xl: 20px;

  /* Paddings */
  --padding-7xs: 6px;
  --padding-11xl: 30px;
  --padding-11xs: 2px;
}
